<template>
  <div class="app">
    <div class="top">
      <input
        type="text"
        placeholder="Search..."
        id="placeinput"
        v-model="query"
        @keyup.enter="getWeather"
      />
    </div>
    <div class="main">
      <div v-if="loaded" v-bind:style="{invisible: !loaded}" class="widget">
        <p class="location">{{ weather.name }}, {{ weather.sys.country }}</p>
        <p class="date">{{ date }}</p>
        <p class="degrees">{{ Math.round(weather.main.temp) }}°C {{ weather.weather[0].main }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      api_key: "55145b84cb966009882d87ada5dcad76",
      loaded: false,
      url_base: "https://api.openweathermap.org/data/2.5/",
      query: "",
      weather: {},
      date: ""
    };
  },
  methods: {
    async getWeather() {
      let res = await fetch(
        this.url_base +
          "weather?q=" +
          this.query +
          "&units=metric&APPID=" +
          this.api_key
      );
      let data = await res.json();
      this.weather = data;
      this.setResults();
    },
    setResults() {
      // let results = this.weather;
      this.loaded = true;

      let d = new Date();
      let months = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ];
      let days = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday"
      ];
      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();

      this.date = `${day} ${date} ${month} ${year}`;
    }
  }
};
</script>

